<template>
  <div class="content">
    <div class="category">
      <div
        v-for="(item, index) in categoryList"
        :key="index"
        :class="{
          category__item: true,
          'category__item--active': currentTab === item.tab,
        }"
        @click="handleCategoryClick(item.tab)"
      >
        {{ item.name }}
      </div>
    </div>
    <div class="product">
      <div v-for="(item, index) in list" :key="index" class="product__item">
        <img class="product__item__img" :src="item.imgurl" alt="" />
        <div class="product__item__detail">
          <h4 class="product__item__title">{{ item.name }}</h4>
          <p class="product__item__sales">月售 {{ item.sales }} 件</p>
          <p class="product__item__price">
            <span class="product__item__yue">&yen;</span>{{ item.price }}
            <span class="product__item__origin">&yen;{{ item.oldprice }}</span>
          </p>
        </div>
        <div class="product__number">
          <span class="product__number__minus">-</span>
          0
          <span class="product__number__add">+</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from 'vue'
import { get } from '@/utils/request'
import { useRoute } from 'vue-router'

// 获取商品列表
const useGetShopGoodEffect = (shopId) => {
  const data = reactive({ list: {} })
  const handleGetGoodList = async (tab) => {
    const result = await get(`/api/shop/${shopId}/products`, { tab })
    if (result.code === 200) {
      data.list = result.data
    }
  }
  const { list } = toRefs(data)
  return { list, handleGetGoodList }
}

// 获取分类列表
const handleGetCategoryEffect = (shopId) => {
  const data = reactive({ categoryList: {} })
  const handleGetCategory = async () => {
    const result = await get(`/api/shop/${shopId}/category`, {})
    if (result.code === 200) {
      data.categoryList = result.data
    }
  }
  const { categoryList } = toRefs(data)
  return { categoryList, handleGetCategory }
}

// 点击获取分类列表
const handleCategoryClickEffect = (handleGetGoodList) => {
  const data = reactive({ currentTab: 'all' })
  const handleCategoryClick = (tab) => {
    handleGetGoodList(tab)
    data.currentTab = tab
  }
  const { currentTab } = toRefs(data)
  return { currentTab, handleCategoryClick }
}

export default {
  name: 'Content',
  setup() {
    const route  =  useRoute()
    const shopId = route.params.id
    const { list, handleGetGoodList } = useGetShopGoodEffect(shopId)
    const { categoryList, handleGetCategory } = handleGetCategoryEffect(shopId)
    const { currentTab, handleCategoryClick } = handleCategoryClickEffect(handleGetGoodList)
    handleGetGoodList()
    handleGetCategory()
    return { currentTab, handleCategoryClick, list, categoryList, handleGetGoodList }
  }
}
</script>
<style lang="scss" scoped>
@import "../../style/mixins.scss";
.content {
  display: flex;
  position: absolute;
  top: 1.5rem;
  bottom: 0.5rem;
  left: 0;
  right: 0;
}
.category {
  height: 100%;
  width: 0.75rem;
  background: #f5f5f5;
  overflow-y: scroll;

  &__item {
    text-align: center;
    line-height: 0.5rem;
    font-size: 0.14rem;
    color: #333;
    &--active {
      background: #fff;
    }
  }
}
.product {
  flex: 1;
  overflow-y: scroll;
  &__item {
    display: flex;
    position: relative;
    padding: 0.12rem 0;
    margin: 0 0.16rem;
    border-bottom: 0.01rem solid #f1f1f1;
    &__detail {
      overflow: hidden;
    }
    &__img {
      width: 0.68rem;
      height: 0.68rem;
      margin-right: 0.16rem;
    }
    &__title {
      margin: 0;
      line-height: 0.2rem;
      font-size: 0.14rem;
      color: #333;
      @include ellipse;
    }
    &__sales {
      margin: 0.06rem 0;
      line-height: 0.16rem;
      font-size: 0.12rem;
      color: #333;
    }
    &__price {
      line-height: 0.2rem;
      margin: 0;
      font-size: 0.14rem;
      color: red;
    }
    &__yen {
      font-size: 0.12rem;
    }
    &__origin {
      margin-left: 0.06rem;
      font-size: 0.1rem;
      color: #999;
      text-decoration: line-through;
    }
  }
  &__number {
    position: absolute;
    right: 0;
    bottom: 0.12rem;
    &__add,
    &__minus {
      width: 0.2rem;
      height: 0.2rem;
      display: inline-block;
      border-radius: 50%;
      font-size: 0.2rem;
      text-align: center;
      line-height: 0.16rem;
    }
    &__minus {
      border: 1px solid #666;
      color: #666;
      margin-right: 0.05rem;
    }
    &__add {
      background: #0091ff;
      color: #fff;
      margin-left: 0.05rem;
    }
  }
}
</style>
