<template>
  <div class="wrapper">
    <div class="search">
      <div class="search__back iconfont" @click="handleBackClick()">
        &#xe601;
      </div>
      <div class="search__content">
        <span class="search__content__icon iconfont">&#xe693;</span>
        <input class="search__content__input" placeholder="请输入商品名称" />
      </div>
    </div>

    <!-- 商品信息组件 -->
    <ShopInfo :items="item" :hideBorder="true" />
    <!-- 商品内容组件 -->
    <Content />
    <!-- 购物车组件 -->
    <Cart />

  </div>
</template>
<script>
import router from '@/router'
import ShopInfo from '../../components/ShopInfo'
import Content from './Content'
import Cart from './Cart'
import { reactive, toRefs } from 'vue'
import { get } from '@/utils/request'
import { useRoute } from 'vue-router'

const useShopInfoEffect = () => {
  const route = useRoute()
  console.log(route.params)
  const data = reactive({ item: {} })
  const handleGetShopInfo = async () => {
    const result = await get(`/api/shop/${route.params.id}`)
    if (result.code === 200) {
      // console.log(result)
      data.item = result.data
    }
  }
  const { item } = toRefs(data)
  // console.log(item.imgurl)
  return { item, handleGetShopInfo }
}
export default {
  name: 'Shop',
  components: { ShopInfo, Content, Cart},
  setup() {
    // const items =
    // {
    //   'imgurl': 'http://www.dell-lee.com/imgs/vue3/near.png',
    //   'title': '第一百货',
    //   'tags': ['月售一万+', '起送0元', '基础运费5元'],
    //   'desc': 'VIP尊享满89元减4元运费券 (每月3张)'
    // }
    const handleBackClick = () => {
      router.back()
    }
    const { item, handleGetShopInfo } = useShopInfoEffect()
    handleGetShopInfo()
    return { item, handleGetShopInfo, handleBackClick }
  }
}
</script>
<style lang="scss" scoped>
@import "../../style/viriables.scss";
.wrapper {
  padding: 0 0.18rem;
}
.search {
  display: flex;
  line-height: 0.32rem;
  margin: 0.14rem 0 0.04rem 0;
  // border: 1px solid red;
  &__back {
    height: 0.32rem;
    // background: #000;
    font-size: 0.24rem;
    color: #b6b6b6;
  }
  &__content {
    display: flex;
    flex: 1;
    background: #f5f5f5;
    line-height: 0.32rem;
    border-radius: 0.16rem;
    &__icon {
      width: 0.44rem;
      text-align: center;
      color: #b7b7b7;
    }
    &__input {
      display: block;
      width: 100%;
      border: none;
      outline: none;
      background: none;
      height: 0.32rem;
      color: #333;
      font-size: 0.14rem;
      &::placeholder {
        color: #333;
      }
    }
  }
}
</style>
